<template>
  <main class="feed" v-on:scroll="scroll">
  	<div class="feed__grid">
      <div class="feed__grid-item" v-for="photo in photos">
          <img :src="photo.urls.small" alt="">
      </div>

      <div class="loading">loading ...</div>
    </div>
  </main>
</template>

<script>
	import axios from 'axios'
	export default {
	name: 'Feed',
    data ()  {
    	return {
        appTitle: 'Awesome App',
        photos: [],
    	totalPhotos: 0,
    	perPage: 12,
    	currentPage: 1,
    	allowToFetch: false
    }
    },
    mounted ()  {
    	this.getPosts(this.currentPage)
    },
    updated ()  {
    	console.log('photos added')
    },
    	
    methods: {
    	getPosts (page) {
    		let options = {
        		params: {
          			client_id: '1504bf5877fd6ef4bb4bd3cb83cc9fdc89258ee0ec9456555f90b083fc065a44',
          			id: 1138699,
          			page: page,
          			per_page: this.perPage
        		}
      		}
      		axios.get(`https://api.unsplash.com/collections/${options.params.id}/photos`, options)
  			.then(response => {
    			this.photos = this.photos.concat(response.data)
        		this.totalPhotos = parseInt(response.headers['x-total'])
        		this.currentPage = page
        		this.allowToFetch = true
  			})
  			.catch(error => {
    			console.log(error);
  			});
    	},
    	scroll (event) {
    		let feedWrapper = event.target
    

    		// getPost 
    		if (feedWrapper.firstElementChild.offsetHeight - feedWrapper.offsetHeight <= feedWrapper.scrollTop&this.allowToFetch == true) {

    			// check pages
    			if ((this.currentPage + 1)*12 <= this.totalPhotos) {
    				// get photos
    				this.allowToFetch = false
    				console.log('get ',this.currentPage + 1)
					this.getPosts(this.currentPage + 1)
    			} else { return }
    			

			}
    	}
    },
  }
</script>
<style lang="scss" src="./style.scss" scoped></style>


